<template>
    <div>
      <!-- 面包屑导航 -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item>商品管理</el-breadcrumb-item>
          <el-breadcrumb-item>商品分类</el-breadcrumb-item>
          </el-breadcrumb>
      <el-card>
        <!-- 添加分类Button -->
        <el-button type="primary" @click="addCateGoriesopen">添加分类</el-button>
        <!-- 添加分类Button 弹窗 -->
        <el-dialog
          title="添加分类"
          :visible.sync="addCateGoriesshow"
          width="50%"
          @close="addCateGoriesclose">
          <el-form
             ref="addCateGoriesRef"
            :model="addCateGoriesInfo"
            :rules="addCateGoriesRules"
            label-width="70px">
            <el-form-item label="" prop="cat_pid">
              <el-input v-model="addCateGoriesInfo.cat_pid"></el-input>
            </el-form-item>
            <el-form-item label="" prop="cat_name">
              <el-input v-model="addCateGoriesInfo.cat_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="cat_level">
              <el-input v-model="addCateGoriesInfo.cat_level"></el-input>
            </el-form-item>
          </el-form>
            <div slot="footer" class="dialog-footer">
              <el-button @click="addCateGoriesclose">取 消</el-button>
              <el-button type="primary" @click="addCateGories">确 定</el-button>
              </div>
        </el-dialog>
        <!-- 商品分类列表 -->
        <el-table
        :data="CateGoriesList"
        style="width: 100%"
        border
        stripe
        row-key="cat_id"
        :tree-props="{cat_level: '0', cat_level: '1', cat_level: '2'}">
          <el-table-column type="index" :index="CateGoriesList.cat_level" label="#"></el-table-column>
          <el-table-column prop="cat_name" label="分类名称" width = "300"></el-table-column>
          <el-table-column prop="cat_deleted" label="是否有效" width = "300">
            <template slot-scope="scope">
            <el-switch v-model="scope.row.cat_deleted" @change="changeDeleted(scope.row)"></el-switch>
            </template>
             </el-table-column>
          <el-table-column prop="cat_level" label="排序" width = "300">
            <template slot-scope="scope">
            <el-tag v-if="scope.row.cat_level == 0" >一级</el-tag>
            <el-tag v-if="scope.row.cat_level == 1" type="success">二级</el-tag>
            <el-tag v-if="scope.row.cat_level == 2" type="warning">三级</el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="username" label="操作" width = "300">
            <template slot-scope="scope">
              <el-button  @click="editCategoriesOpen" type="primary" icon="el-icon-edit">编辑</el-button>
              <el-button type="danger" icon="el-icon-delete">删除</el-button>
<!-- .
.
.               这是编辑分类弹窗
.
. -->
            <el-dialog
              title="编辑分类"
              :visible.sync="editCategoriesShow"
              width="50%"
              @close="editCategoriesClose">
                <el-form
                   ref="editCategoriesRef"
                   :model="editCategoriesInfo"
                   :rules="editCategoriesRules"
                   label-width="70px">
                  >
                  <el-form-item></el-form-item>
                  </el-form>
                <div slot="footer" class="dialog-footer">
                <el-button @click="editCategoriesClose">取 消</el-button>
                <el-button type="primary" @click="getEditCategories(scope.row.cat_id)">确 定</el-button>
                </div>
            </el-dialog>

            </template>
          </el-table-column>
        </el-table>
        <!-- @size-change="changeSize"         页面每次展示页面大小改变时的事件
        @current-change="changeCurrent"   页面页码数改变时的事件 -->
        <el-pagination
            @size-change="changeSize"
            @current-change="changeCurrent"
            :current-page="this.getCateGoriesSet.pagenum"
            :page-size="this.getCateGoriesSet.pagesize"
            :page-sizes="[10, 20, 30, 40]"
            layout="total, sizes, prev, pager, next, jumper"
            :total="this.getCateGoriesSet.total">
          </el-pagination>
      </el-card>
    </div>
</template>
<script>

export default {
  data () {
    return {
      CateGoriesList: [],
      getCateGoriesSet: {
        type: null,
        pagenum: null,
        pagesize: null,
        total: null
      },
      addCateGoriesshow: false,
      addCateGoriesInfo: {
        cat_pid: '',
        cat_name: '',
        cat_level: ''
      },
      addCateGoriesRules: {
        cat_pid: [],
        cat_name: [],
        cat_level: []
      },
      editCategoriesInfo: {
        cat_name: ''
      },
      editCategoriesShow: false,
      editCategoriesRules: {
        cat_name: []
      }
    }
  },
  created () {
    this.getCateGoriesList()
  },
  methods: {
    // 获取商品分类列表
    async getCateGoriesList () {
      const { data: res } = await this.$http.get('categories', this.getCateGoriesSet)
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.CateGoriesList = res.data
      this.CateGoriesList.total = res.data.length
      console.log(this.CateGoriesList)
    },
    addCateGoriesopen () {
      this.addCateGoriesshow = true
    },
    addCateGoriesclose () {
      this.addCateGoriesshow = false
    },
    async addCateGories () {
      const { data: res } = await this.$http.post('categories', this.addCateGoriesInfo)
      if (res.meta.status !== 201) return this.$message.error(res.meta.msg)
      this.getCateGoriesList()
      this.addCateGoriesclose()
      this.$message.success(res.meta.msg)
    },
    changeDeleted (row) {
      row.cat_deleted = !row.cat_deleted
    },
    editCategoriesOpen () {
      this.editCategoriesShow = true
    },
    editCategoriesClose () {
      this.editCategoriesShow = false
    },
    async getEditCategories (id) {
      console.log('分类ID' + id)
      const { data: res } = await this.$http.put(`categories/${id}`, this.editCategoriesInfo)
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.getCateGoriesList()
      this.editCategoriesClose()
      this.$message.success(res.meta.msg)
    },

    changeSize (newSize) {
      this.getCateGoriesSet.pagesize = newSize
      this.getCateGoriesList()
    },
    changeCurrent (newPage) {
      this.getCateGoriesSet.pagenum = newPage
      this.getCateGoriesList()
    }
  },
  comments: {
  }
}
</script>

<style lang='less' scoped>

</style>
