</template>
<script>

export default {
  data () {
    return {
      goodsList: {},
      getGoodset: {
        query: '',
        pagenum: '0',
        pagesize: '10'
      }
    }
  },
  created () {
    this.getGoodsList()
  },
  methods: {
    async getGoodsList () {
      console.log(this.getGoodset)
      const { data: res } = await this.$http.get('goods', this.getGoodset)
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.goodsList = res.data
      console.log(this.goodsList)
    }
  },
  comments: {
  }
}
</script>

